
.rank {
  position: fixed;
  width: 100%;
  top: 88px;
  bottom: 0;
}
.rank .toplist {
  height: 100%;
  overflow: hidden;
}
.rank .toplist .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 20px;
  padding-top: 20px;
  height: 100px;
}
.rank .toplist .item:last-child {
  padding-bottom: 20px;
}
.rank .toplist .item .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100px;
          flex: 0 0 100px;
  width: 100px;
  height: 100px;
}
.rank .toplist .item .songlist {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0 20px;
  height: 100px;
  overflow: hidden;
  background: #333;
  color: rgba(255,255,255,0.3);
  font-size: 12px;
}
.rank .toplist .item .songlist .song {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  line-height: 26px;
}


